html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: 2.625rem;
  font-size: clamp(2.625rem, 1.77rem + 3.64vw, 4.5rem);
}

h2 {
  font-size: 2.25rem;
  font-size: clamp(2.25rem, 1.91rem + 1.45vw, 3rem);
}

h3 {
  font-size: 1.5rem;
}

p {
  margin-top: 0;
  font-size: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: underline;
}

:root {
  --main-bg: hsla(0, 0%, 100%, 1);
  --text-dark: hsla(233, 14%, 25%, 1);
  --text-light: hsla(0, 0%, 100%, 1);
  --hero-bg: hsla(232, 58%, 55%, 1);
  --button-primary-bg: hsla(180, 100%, 42%, 1);
  --button-primary-text: hsla(233, 14%, 25%, 1);
  --button-secondary-bg: transparent;
  --button-secondary-border: hsla(0, 0%, 100%, 1);
  --button-secondary-text: hsla(0, 0%, 100%, 1);
  --button-cta-bg: hsla(0, 0%, 100%, 1);
  --button-cta-text: hsla(233, 14%, 25%, 1);
  --fullwidth-bg: hsla(320, 85%, 41%, 1);
  --cta-desktop: linear-gradient(
    90deg,
    hsla(233, 14%, 25%, 1) 0%,
    hsla(232, 58%, 55%, 1) 56.34%,
    hsla(180, 100%, 42%, 1) 112.47%
  );
  --cta-mobile: linear-gradient(
    90deg,
    hsla(233, 14%, 25%, 1) 0%,
    hsla(232, 58%, 55%, 1) 76.28%,
    hsla(180, 100%, 42%, 1) 152.28%
  );
}

.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
}